<template>
  <div class="demo-vsteper-container">
    <h2></h2>
    <mu-stepper :activeStep="activeStep" orientation="vertical">
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="http://www.runoob.com/html/html-tutorial.html">HTML</a>
        </mu-step-label>
        <mu-step-content>
          <p>
            HyperText Markup LanguageHTML HTML  WEB HTML 
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="http://www.runoob.com/css/css-tutorial.html">CSS</a>
        </mu-step-label>
        <mu-step-content>
          <p>
            (Cascading Style Sheets)HTMLXMLCSS
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
          <mu-flat-button label="" class="demo-step-button" @click="handlePrev" />
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="http://www.runoob.com/js/js-tutorial.html">Javascript</a>
        </mu-step-label>
        <mu-step-content>
          <p>
            JavaScriptJavaScriptHTMLHTML
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
          <mu-flat-button label="" class="demo-step-button" @click="handlePrev" />
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="https://cn.vuejs.org/">Vue</a>
        </mu-step-label>
        <mu-step-content>
          <p>
            Vue.js /vju/ view Vue Vue  Vue Vue 
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
          <mu-flat-button label="" class="demo-step-button" @click="handlePrev" />
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="http://www.muse-ui.org/#/index">Muse-UI</a>
        </mu-step-label>
        <mu-step-content>
          <p>
            Muse UI  Material Design  Vue 2.0 
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
          <mu-flat-button label="" class="demo-step-button" @click="handlePrev" />
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          
          <a target="_blank" href="https://github.com/kessil/exam_assistant"></a>
        </mu-step-label>
        <mu-step-content>
          <p>
            
          </p>
          <mu-raised-button label="" class="demo-step-button" @click="handleNext" primary/>
          <mu-flat-button label="" class="demo-step-button" @click="handlePrev" />
        </mu-step-content>
      </mu-step>
    </mu-stepper>
    <h3 v-if="finished" class="finished">
      !
      <a href="javascript:;" @click="reset"></a>
      <div v-if="false">
        <p>"../data.json"json:</p>
        <img :src="dataExampleImg.src" :alt="dataExampleImg.name" :title="dataExampleImg.name" width="100%">
      </div>
    </h3>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeStep: 0,
      dataExampleImg: { name: "", src: require('../assets/data.png') },
      linkerImg: [
        { name: "https://cn.vuejs.org/", src: require('../assets/logo_vue.png') },
        { name: "http://www.muse-ui.org/#/index", src: require('../assets/logo_muse-ui.png') },
        { name: "https://github.com/kessil/exam_assistant", src: require('../assets/logo_github.png') }
      ]
    }
  },
  computed: {
    finished() {
      return this.activeStep > 5
    }
  },
  methods: {
    handleNext() {
      this.activeStep++
    },
    handlePrev() {
      this.activeStep--
    },
    reset() {
      this.activeStep = 0
    }
  }
}
</script>

<style lang="css" scoped>
.demo-vsteper-container {
  max-width: 380px;
  /* max-height: 400px; */
  margin: auto;
}

.finished {
  margin-top: 0.5rem;
}

.demo-step-button {
  margin-top: 12px;
  margin-right: 12px;
}

p {
  text-align: left;
}
</style>




